import { Component, OnInit } from '@angular/core';

import { FormGroup, FormControl, Validators, FormArray } from '@angular/forms';
import { Horario } from './horario';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit {

  public form: FormGroup;

  public totalHoras: Date;

  public horario;

  public hourMinuteMask = [/[0-2]/, /\d/, ':', /\d/, /\d/];

  public errors: Array<string>;

  ngOnInit() {
    this.form = new FormGroup({
      entrada: new FormControl(null, Validators.required),
      saida: new FormControl(null, Validators.required),
      intervalos: new FormArray([])
    });
    this.horario = new Horario();
    this.horario.entrada = new Date();
    this.horario.saida = new Date();
    this.horario.intervalos = new Array<Horario>();
    this.errors = new Array();
  }

  public get intervalos(): FormArray {
    return this.form.get('intervalos') as FormArray;
  }

  public addIntervalo(intervalo?: Horario) {
    const dependenteFormGroup = new FormGroup({
      nome: new FormControl(null),
      entrada: new FormControl(null, Validators.required),
      saida: new FormControl(null, Validators.required),
    });
    this.intervalos.push(dependenteFormGroup);
  }

  public deleteIntervalo(index: number) {
    this.intervalos.removeAt(index);
    this.atualizarHorarios();
  }

  public atualizarHorarios(index?: number) {
    let horariosForm = { ...this.form.value };

    this.horario.entrada = this.getDateNowWithCustomHour(horariosForm.entrada);
    this.horario.saida = this.getDateNowWithCustomHour(horariosForm.saida);
    this.validarHorario('horario', this.horario.entrada, this.horario.saida);

    this.horario.total = this.getTotal(this.horario.entrada, this.horario.saida);
    let somaIntervalosHoras = 0;
    let somaIntervalosMinutos = 0;
    let intervalos = new Array<Horario>();
    horariosForm.intervalos.map(intervaloForm => {
      let intervalo = new Horario();
      intervalo.index = index;
      intervalo.nome = intervaloForm.nome;
      intervalo.entrada = this.getDateNowWithCustomHour(intervaloForm.entrada);
      intervalo.saida = this.getDateNowWithCustomHour(intervaloForm.saida);
      this.validarHorario(index, intervalo.entrada, intervalo.saida);
      intervalo.total = this.getTotal(intervalo.entrada, intervalo.saida);
      somaIntervalosHoras += intervalo.total.getHours();
      somaIntervalosMinutos += intervalo.total.getMinutes();
      intervalos.push(intervalo);
    });
    this.horario.intervalos = intervalos;
    this.totalHoras = new Date();
    this.totalHoras.setHours(this.horario.total.getHours() - somaIntervalosHoras);
    this.totalHoras.setMinutes(this.horario.total.getMinutes() - somaIntervalosMinutos);
    console.log(this.errors);
  }

  private validarHorario(index, entrada, saida) {
    if (entrada > saida) {
      this.errors.push(index);
    } else {
     this.errors.splice(this.errors.indexOf(index));
    }
  }

  private getDateNowWithCustomHour(customHour) {
    let dateNow = new Date();
    if (customHour) {
      let explodedHour = customHour.split(':');
      if (explodedHour.length > 1) {
        dateNow.setHours(explodedHour[0], explodedHour[1]);
      }
    }
    return dateNow;
  }

  private getTotal(initDate: Date, endDate: Date) {
    let totalDate = new Date();
    let hours = endDate.getHours() - initDate.getHours();
    let minutes = endDate.getMinutes() - initDate.getMinutes();
    totalDate.setHours(hours);
    totalDate.setMinutes(minutes);
    return totalDate;
  }


}



